.TodoTemplate {
    width: 512px;
    text-align: center;
    margin: 5rem auto;
    padding: 15px;
    overflow: hidden;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.3);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    .title {
        margin-top: 10px;
        h1 {
            font-weight: bold;
            color: #ee8484;
        }

        p {
            color: #f3b4b4;
            margin-top: 5px;
            font-weight: 500;
        }
    }
}