.TodoListItem {
    border-radius: 3px;
    margin-bottom: 20px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    background-color: rgba(255, 255, 255, 0.5);
    display: flex;
    align-items: center;
    padding: 1.3rem;

    div {
        display: flex;
        align-items: center;
    }

    .checkbox {
        flex: 1;
        cursor: pointer;
        transition: 0.2s all ease-in;

        svg {
            font-size: 1.7rem;
            color: #ee8484;
            background-color: transparent;
        }
        .text {
            flex: 1;
            margin-left: 1.3rem;

            input {
                outline: none;
                border: none;
                font-size: 1rem;
                font-weight: 600;
                color: #ee8484;
                background-color: transparent;
            }
        }

        &.checked {
            svg {
                color: #999;
                background-color: transparent;
            }

            .text {
                text-decoration: line-through;

                input {
                    color: #999;
                }
            }
        }
    }

    .remove {
        cursor: pointer;

        svg {
            font-size: 1.3rem;
            color: #777;
        }
    }

    .create {
        margin-left: 10px;
        cursor: pointer;

        svg {
            font-size: 1.3rem;
            color: #777;
        }
    }
}