.TodoInsert {
    margin-top: 30px;

    .todo {
        display: flex;
        align-items: center;
        padding: 5px 8px;
        border-radius: 3px;
        background-color: rgba(245, 180, 180, 0.7);
        box-shadow: inset rgba(102, 102, 102, 0.3) 0 0 5px;
        margin: 28px 0;

        button {
            outline: none;
            border: none;
            background: transparent;
            font-size: 1.8rem;
            line-height: 1.5;

            display: flex;
            align-items: center;
            cursor: pointer;
            transition: 0.2s all ease-in;
            color: #fff;
            margin-right: 15px;

            &:hover {
                color: #999;
            }
        }
        
        input {
            outline: none;
            border: none;
            background-color: transparent;
            height: 17px;
            padding-top: 5px;
            font-size: 18px;
            color: #fff;

            &::placeholder {
                font-size: 18px;
                color: #fff;
            }
        }
    }
}